@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');


.clients {
    color: #dfd2b8;  
    height: 40vh;
    min-height: 50vh;
    text-align: center;
    width: 100%;
}

.container{
    padding-top: 150px;
}

h2 {
    text-align: center;
    padding: 50px;
    font-size: 50px;
    font-weight: 600;
}

.slick-slide{
    margin: 0 20px;
}

.slick-slide iframe{
    text-align: center;
    width: 100%;
    min-height: 20vh;
    padding-bottom: 50px;
    
}
.slick-slider{
    position: relative;
    display: block;
    box-sizing: border-box;
}
.slick-list{
    position: relative;
    display: block;
    overflow: hidden;
    margin: 0;
    padding: 0;
}
.slick-track{
    position: relative;
    top: 0;
    left: 0;
    display: block;
    
}
.slick-slide{
    display: none;
    float: left;
    height: 100%;
    min-height: 1px;
    
}
.slick-slide img{
    display: block;
}
.slick-initialized .slick-slide{
    display: block;
}
.copy{
    padding-top: 250px;
}